import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'sa-progress',
  template: require('./progress.component.html')
})
export class ProgressComponent implements OnInit, OnChanges {

  private barClass: string;
  private barWidth: string;

  @Input()
  private align: string = ''; // ['', 'right', 'bottom']

  @Input()
  private vertical: boolean = false; // 

  @Input()
  private striped: boolean = false; // 

  @Input()
  private active: boolean = false; // 

  @Input()
  private size: string = ''; // ['micro', 'xs', 'sm', '', 'lg']

  @Input()
  private maxValue: number = 100; // 

  @Input()
  private type: string = ''; // ['danger', 'warning', 'success', 'info', 'primary']

  @Input()
  private class: string = ''; // class

  @Input()
  private value: number; // value

  @Input()
  private bgColorClass: string = ''; // 

  ngOnInit() {

  }

  ngOnChanges(changesObj: SimpleChanges) {
    if (changesObj.value || changesObj.maxValue) {
      this.calcBarWidth();
    }
    this.calcBarClass();
  }

  private calcBarWidth(): void {
    let per = this.value / this.maxValue * 100;
    if (per !== per) { // Is NaN
      return this.barWidth = null;
    }
    if (per > 100) {
      per = 100;
    }
    this.barWidth = `${per}%`;
  }

  private calcBarClass() {
    let classArr = [];
    if (this.type) {
      classArr.push(`progress-bar-${this.type}`);
    }
    if (this.size) {
      classArr.push(`progress-${this.size}`);
    }
    if (this.align) {
      classArr.push(this.align);
    }
    if (this.vertical) {
      classArr.push('vertical');
    }
    if (this.striped) {
      classArr.push('progress-striped');
    }
    if (this.active) {
      classArr.push('active');
    }
    if (this.class) {
      classArr.push(this.class);
    }
    this.barClass = classArr.join(' ');
  }
}



